/* Plate General */
.plate {
    height: 149px;
    border-radius: 9px;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -webkit-touch-callout: none;
    box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
}

.plate--text {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.plate--amount {
    font-size: 12px;
}

/* Plate Display Status */

.plate--on {
    display: inherit;
}

.plate--off {
    display: none;
}


/* Plate Widths */

.plate--width-large {
    width: 30px;
}

.plate--width-medium {
    width: 25px;
}

.plate--width-small {
    width: 21px;
}

/* Specific Plate Types */

.plate2point5 {
    background-color: #9f9f9fc3;
    color: black;
    font-size: small;
}

.plate5 {
    background-color: #464646;
}

.plate10 {
    background-color: black;
}

.plate25 {
    background-color: #67A074;
}

.plate35 {
    background-color: #DFBD0A;
}

.plate45 {
    background-color: #6170ff;
}

.plate55 {
    background-color: #ff6161;
}